import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { StagesComponent } from './stages/stages.component';
import { EtudesComponent } from './etudes/etudes.component';
import { ProfilComponent } from './profil/profil.component';
import { ElectroniqueComponent } from './electronique/electronique.component';
import { TechnologiesComponent } from './technologies/technologies.component';
import { ProjectsComponent } from './projects/projects.component';
import { DiplomesComponent } from './diplomes/diplomes.component';
import {contactService} from './services/app.services';
import { PostsComponent } from './posts/posts.component';
import { Routes, RouterModule } from '@angular/router';


const appRoutes: Routes = [
  { path: 'profile', component: ProfilComponent},
  { path: 'stage', component: StagesComponent },
  { path: 'etudes', component: EtudesComponent },
  { path: 'electronique', component: ElectroniqueComponent },
  { path: 'contacter', component: PostsComponent },
  { path: 'projets', component: ProjectsComponent },
  { path: 'projets/:id/:nom', component:TechnologiesComponent },
  { path: '', component: ProfilComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    StagesComponent,
    EtudesComponent,
    ProfilComponent,
    ElectroniqueComponent,
    TechnologiesComponent,
    ProjectsComponent,
    DiplomesComponent,
    PostsComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [contactService],
  bootstrap: [AppComponent]
})
export class AppModule { }
